@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Colors
#256e5e Greenish blue
#a6a3b6 Greyish purple
*/
.App {
  min-height: 100vh;
  width: 100%;
  background-color: #256e5e;
  margin: 0;
  padding: 0;
  background-image: url(/zackkomo.github.io/static/media/homebackground.7ddbe64b.jpg);
  height: 95vh;
  width: 100%;
  background-size: cover;
}
.homepage {
  position: relative;
}
.homepageContent {
  font-family: "Redressed";
  position: relative;
  padding-top: 30vh;
}

.mybanner {
  height: 30vh;
  background-color: #a6a3b6;
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;
}

.mybanner h1 {
  font-size: 10vh;
}

.mybanner h3 {
  font-size: 4vh;
}

.particles {
  position: absolute;
  height: 100%;
  width: 100%;
}

section.container {
  background-color: #256e5e;
  min-height: 70vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  width: 80vw;
  margin-top: 5vh;
  border-radius: 25px;
  margin-left: 10vw;
  overflow-x: hidden;
}
section.container-content {
  text-align: justify;
  margin-top: 5vh;
  min-height: 70vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}
section.container-content p {
  margin: 0px 20px;
}
section.container-content h1 {
  margin: 0;
  padding: 0;
  text-align: center;
}

.photo {
  height: 200px;
  width: 200px;
  margin: 0px 10px;
  float: left;
}

.project {
  max-height: 275px;
  max-width: 50vw;
  margin: 0px 10px;
  float: left;
}
.picture-container {
  max-height: 250px;
  margin: 0px 10px;
  max-width: 50vw;
  scrollbar-width: 20px;
  float: left;
  overflow: hidden;
}
.pdf-container {
  max-height: 250px;
  margin: 0px 10px;
  max-width: 50vw;
  scrollbar-width: 20px;
  float: left;
  overflow: hidden;
}

.contact-form {
  min-height: 30vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  width: 80%;
  display: inline;
}
.contact-email {
  width: 45vw;
  height: 25px;
}
.contact-text {
  resize: vertical;
  width: 45vw;
  min-height: 40vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}
.contact-form-button {
  width: 45vw;
  background-color: white;
  margin-bottom: 20px;
}

.box {
  display: flex;
  width: 20vh;
  height: 20vh;
  background-color: rgb(68, 121, 139);

  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 3rem;
  border: 3px solid black;
  transition: background 0.2s ease-in-out;
}

.grid {
  display: grid;
  grid-template-columns: auto auto auto;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

button {
  justify-content: center;
  align-items: center;
  display: flex;
  outline: none;
  padding: 10px 20px;
  border: none;
  outline: 0px;
  font-size: 1rem;
  font-weight: bold;
  background: none;
  transition: all 0.2s ease-in-out;
  width: 20vh;
  text-align: center;
}

button:hover {
  cursor: pointer;
  background: rgb(46, 124, 22);
  color: white;
}
.occupied {
  display: flex;
  width: 20vh;
  height: 20vh;
  background-color: rgb(68, 121, 139);

  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 3rem;
  border: 3px solid black;
  transition: background 0.2s ease-in-out;
}
.occupied:hover {
  background: #ff3a3a;
}

#Box_0,
#Box_1,
#Box_2 {
  border-top: none;
}

#Box_0,
#Box_3,
#Box_6 {
  border-left: none;
}

#Box_6,
#Box_7,
#Box_8 {
  border-bottom: none;
}

#Box_2,
#Box_5,
#Box_8 {
  border-right: none;
}

.h1 {
  align-content: center;
  text-align: center;
}

.h2 {
  align-content: center;
  text-align: center;
}

